import { useParams } from "react-router-dom";
import React from "react";


const Product = ({ data }) => {
  const { productId } = useParams();
  const product = data.find((p) => p.id === Number(productId));
  let productData;

  if (product) {
    productData = (
        <div className="bg-white  "  >


<div className="container-fluid ">

<div >
  

<img src={product.imageUrl} height={350} alt="bottled products" />

</div>

<div className="text-left">
<h1><strong>
          {product.name}
            </strong></h1>
        <p><strong>DERSCRIPCION:</strong> {product.description}</p>
        <p><strong>MODO DE USO:</strong> {product.usage}</p>
        <p><strong>INGREDIENTES:</strong> {product.ingredients}</p>
        <h5><strong>STATUS:</strong> {product.status}</h5>
        <p className="text-justify">
         <strong> {product.name} </strong>
 {product.content}
        </p>
</div>
    </div>
     </div>

    );
  } else {
    productData = <h2>Perdon. Ese producto no existe. </h2>;
  }
  return (

 <div className="bg-white text-center">
        <div  style={{ display: "flex" }}>
      <div
        style={{
          width: "100%",
          height: "40%",
          margin: "auto",
          background: "#ffffff",
          padding: "10px"
        }}
      >
        {productData}
      </div>
    </div>
    </div>
  );
};

export default Product;
