import React, { Component } from "react";
import {
  Route,
  NavLink,
  HashRouter
} from "react-router-dom";
import Home from "./Home";
import Products from "./Products";
import Nosotros from "./Nosotros";
import Pharm from "./Pharm";
import Salud from "./Salud";
import Estetica from "./Estetica";
import Regeneratur from "./Regeneratur";
import ComingSoon from "./ComingSoon";
import Footer from "./Footer";

import "../style.css"

import geah_logo_gg from "../images/geah_logo_gg.png"



class Main extends Component {
    render() {
      return (

    <div >

    <header>
    <HashRouter basename={process.env.PUBLIC_URL}>
    <nav
    className="navbar
    navbar-toggleable-md
    navbar-light bg-light
    shadow-lg sticky-top">

    <NavLink className="text-white mx-auto"  to="/">
    <img  src={geah_logo_gg }  height={50}   alt="GEAH logo" />
    </NavLink>

      <button className="navbar-toggler navbar-toggler-right" type="button"
      data-toggle="collapse"
      data-target="#navbarNavDropdown"
      aria-controls="navbarNavDropdown"
      aria-expanded="false"
      aria-label="Toggle navigation"
      style={{
        border: '0',
        outline: 'none'
      }}>
        <span
        className="navbar-toggler-icon">

        </span>
      </button>

      <div className="collapse navbar-collapse " id="navbarNavDropdown" >
        <ul className="navbar-nav text-right bg-light" >
        <div className="dropdown">

      <NavLink className="dropdown-item text-right"   to="/">
    INICIO
        </NavLink>

       <NavLink className="dropdown-item text-right" data-toggle="dropdown"  to="/pharm">
        PHARM
        </NavLink>

      <div className="dropdown-menu  dropdown-menu-end container-fluid"  aria-labelledby="dropdownMenuButton" >
      <NavLink className="dropdown-item text-right" to="/products">Todo los productos</NavLink>

          <NavLink className="dropdown-item text-right" to="/regeneratur">Regeneratur Vitamin C</NavLink>

    <NavLink className=" dropdown-item text-right"  to="/comingsoon">Peau D'ange Colágeno hidrolizado + Biotina</NavLink>

    <NavLink className=" dropdown-item text-right" to="/comingsoon">CamuPlus C</NavLink>

    <NavLink className=" dropdown-item text-right" to="/comingsoon">Magnelife Cloruro de Magnesio</NavLink>

    <NavLink className=" dropdown-item text-right" to="/comingsoon">
    </NavLink>

    <NavLink className=" dropdown-item text-right" to="/comingsoon">Peau de'Ange: Premium Fruta Rojas</NavLink>

    </div>
    </div>
          <li className="nav-item bg-light ">
             <span className="p-2">
             <NavLink className="text-dark"  to="/salud">SALUD Y BELLEZA</NavLink>        </span>
            </li>
            <li className="nav-item bg-light ">
             <span className="p-2">
             <NavLink className="text-dark" to="/estetica">MEDICINA ESTETICA </NavLink>          </span>
            </li>
            <li className="nav-item bg-light ">
             <span className="p-2">
             <NavLink className="text-dark" to="/nosotros">NOSOTROS</NavLink>          </span>
            </li>
            </ul>
    <ul>
    </ul>
      </div>
    </nav>
    <div >
      <Route exact path="/" component={Home}/>
      <Route path="/pharm" component={Pharm}/>
      <Route path="/salud" component={Salud}/>
      <Route path="/estetica" component={Estetica}/>
      <Route path="/regeneratur" component={Regeneratur}/>
      <Route path="/products" component={Products}/>
      <Route path="/nosotros" component={Nosotros}/>
      <Route path="/comingsoon" component={ComingSoon}/>
      <Route path="/footer" component={Footer}/>
    </div> 
    </HashRouter>
      </header>
      <Footer />
    </div>
      );
    }
  }

export default Main;